
import { getCurrentInstance, computed } from "vue";

import AtomInput from "@/components/atoms/AtomInput.vue";
import AtomInputLabel from "../atoms/AtomInputLabel.vue";
import AtomCounterForInput from "../atoms/AtomCounterForInput.vue";

export default {
  name: "MoleculeInputWithLabel",
  components: {
    AtomInput,
    AtomInputLabel,
    AtomCounterForInput,
  },
  props: {
    modelValue: {
      type: String,
      default: "",
    },
    type: {
      type: String,
      default: "text",
      validator(value: string) {
        return ["text", "email", "tel", "number"].includes(value);
      },
    },
    placeholder: {
      type: String,
      default: "",
    },
    maxLength: {
      type: Number,
      default: null,
    },
    label: {
      type: String,
      default: "",
    },
    requaied: {
      type: Boolean,
      default: false,
    },
    showCounter: {
      type: Boolean,
      default: false,
    },
  },
  setup() {
    const componentInstance = getCurrentInstance();
    let uid = 0;

    if(componentInstance) {
      uid = componentInstance.uid;
    }

    const inputId = computed(() => `molecule-input-with-label-${uid}`);

    return {
      inputId,
    };
  },
};
