
export default {
  name: "AtomInput",
  props: {
    modelValue: {
      type: String,
      default: '',
    },
    type: {
      type: String,
      default: 'text',
      validator(value: string) {
        return ['text', 'email', 'tel', 'number'].includes(value);
      },
    },
    placeholder: {
      type: String,
      default: ''
    },
    maxLength: {
      type: Number,
      default: null
    },
    inputId: {
      type: String,
      default: ''
    }
  }
}
