
import { getCurrentInstance, computed } from "vue";

import AtomInputLabel from "@/components/atoms/AtomInputLabel.vue";
import AtomTextarea from "@/components/atoms/AtomTextarea.vue";
import AtomCounterForInput from "../atoms/AtomCounterForInput.vue";

export default {
  name: "MoleculeTextareaWithText",
  components: {
    AtomInputLabel,
    AtomTextarea,
    AtomCounterForInput,
  },
  props: {
    modelValue: {
      type: String,
      default: "",
    },
    placeholder: {
      type: String,
      default: "",
    },
    maxLength: {
      type: Number,
      default: null,
    },
    label: {
      type: String,
      default: "",
    },
    rows: {
      type: Number,
      default: 10,
    },
    requaied: {
      type: Boolean,
      default: false,
    },
    showCounter: {
      type: Boolean,
      default: false,
    },
  },
  setup() {
    const componentInstance = getCurrentInstance();
    let uid = 0;

    if(componentInstance) {
      uid = componentInstance.uid;
    }

    const textareaId = computed(() => `molecule-textarea-with-label-${uid}`);

    return {
      textareaId,
    };
  },
};
